import { NgModule } from '@angular/core';
import { CommonModule }      from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { AddAffiliationComponent } from './add-affiliation.component';
import { AffiliationCrudRoutingModule } from './add-affiliation-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    AffiliationCrudRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    AddAffiliationComponent
  ]
})
export class AddAffiliationModule { }



